body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: crimson;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{
  position: fixed;
  margin: auto;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  width: 100%;
  max-width: 480px;
  /*height: 500px;*/
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.outer{
  width: 100%;
  padding: 0 15px;
}

.inner{
  background: white;
  padding: 20px;
  border-radius: 2em;
}

article{
  margin: auto;
  font-size: 18px;
  width: 75%;
}

.tenzies {
  width: 75%;
  border-radius: 2em;
  border: 2px solid crimson;
  margin: 30px auto;
  padding: 15px;
  display: flex;
  flex-wrap: wrap;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: 1fr 1fr;
  grid-gap: 10px;
  justify-content: space-around;
  place-items: center;
}

.dice{
  width: 50px;
  height: 50px;
  background: aquamarine;
  border-radius: .7em;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  grid-gap: 0px;
  place-items: center;
  font-size: 20px;
  font-weight: bold;
  cursor: pointer;

}

.dot{
  width: 8px;
  height: 8px;
  background-color: black;
  border-radius: 50%;
}

.dice.active{
  background: #d05454;
}

.roll{
  width: 200px;
  height: 55px;
  border: 0;
  border-radius: 1em;
  background-color: #c3d3fa;
  color: black;
  font-size: 24px;
  transition: .3s;
  cursor: pointer;
}

.roll:hover{
  background-color: #21b2ea;

}

canvas{
  width: 100%;
  height: 100%;
}

.info{
  margin-top: 30px;
  font-size: 22px;
}

@media screen and (max-width: 444px) {
  .tenzies{
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
}